import {Component, OnInit} from '@angular/core';
import {ApiService} from "../../services/api.service";
import {Storage} from "@ionic/storage";
import {LoaderService} from "../../services/loader.service";
import {environment} from "../../environments/environment";
import {AuthService} from "../../services/auth.service";
import {Location} from "@angular/common";
import {ToastService} from "../../services/toast.service";
import * as moment from 'moment';
import {Platform, MenuController} from "@ionic/angular";
import {Device} from "@ionic-native/device/ngx";
import {AnalyticsService} from "../../services/analytics.service";
import { AlertController } from '@ionic/angular';
import {TranslateService} from "@ngx-translate/core";
import {EventProviderService} from "../../services/event-provider.service";
import {LogoServiceService} from "../../services/logo-service.service";
import {Router} from "@angular/router";

@Component({
    selector: 'app-my-contributions',
    templateUrl: './my-contributions.page.html',
    styleUrls: ['./my-contributions.page.scss'],
})
export class MyContributionsPage implements OnInit {

    loginData: any;
    total_given_year: any;
    total_gift: any;
    average_gift: any;
    most_recent: any;
    donations: boolean = true;
    theme: any;
    ch_id: any;
    allDonations: any;
    allSubscriptions: any;
    temp_subscriptions: any;
    temp_donations: any;
    start_time_analytics: any;
    page_name: any = "My Contributions";
    custom_name: any = "My Contributions";
    user: any;
    alertMessage: any;
    constructor(
        private apiService: ApiService,
        private storage: Storage,
        private loader: LoaderService,
        private Auth: AuthService,
        private location: Location,
        private toast: ToastService,
        private analytics: AnalyticsService,
        private platform: Platform,
        private device: Device,
        public alertController: AlertController,
        private translate: TranslateService,
        private menu: MenuController,
        private event_provider: EventProviderService,
        private logoService: LogoServiceService,
        private router: Router,
    ) {
        // get translated alert message
        let interval = setInterval(() => {
            this.translate.get('ALERTS').subscribe( (res: any) => {
                this.alertMessage = res;
                clearInterval(interval);
            });
        }, 2000)

    }

    ngOnInit() {
        // getting user data
        this.getLoginData();
    }

    async presentAlertConfirm( subscription_id ) {
        const alert = await this.alertController.create({
            cssClass: 'my-custom-class',
            header: 'Confirmation!',
            message: 'Are you sure you want to stop subscription?',
            buttons: [
                {
                    text: 'No',
                    role: 'cancel',
                    cssClass: 'secondary',
                    handler: (blah) => {

                    }
                }, {
                    text: 'Yes',
                    handler: () => {
                        this.stopSubscription(subscription_id);
                    }
                }
            ]
        });

        await alert.present();
    }

    async getLoginData() {
        this.loginData = await this.Auth.getUser();
        if (this.loginData) {
            // getting user from local storage
            this.storage.get("theme").then(res => {
                if (res) {
                    this.theme = res;
                    this.ch_id = this.theme.ch_id;

                    // getting contributions of the user
                    this.getContributionData();
                }
            });

        }
    }

    async getTheme() {
        // getting theme from local storage
        this.storage.get("theme").then(res => {
            if (res) {
                this.theme = res;
                this.ch_id = this.theme.ch_id;
            }
        });
    }

    getContributionData() {
        const auth = {
            authorization: 'Bearer ' + this.loginData.auth.refresh_token
        };

        this.loader.presentLoading().then(() => {
            const body = {
                auth: 'Bearer ' + this.loginData.auth.access_token,
                 id: this.loginData.user.id,
                 ch_id: this.ch_id
            }; 
            // fetching contributions of the user
            this.apiService.getContributions(JSON.stringify(body)).subscribe(async (res: any) => {
                if (res.status) {
                    // =================== Assigning Data ================================================================= //
                    if (res.donations.amount_giving_year !== null && res.donations.amount_giving_year !== "") {
                        this.total_given_year = res.donations.amount_giving_year;
                    } else {
                        this.total_given_year = 0.00;
                    }

                    if (res.donations.total_gift !== null && res.donations.total_gift !== "") {
                        this.total_gift = res.donations.total_gift;
                    } else {
                        this.total_gift = 0.00;
                    }

                    if (res.donations.averagegift !== 0) {
                        this.donations = false;
                        this.average_gift = res.donations.averagegift;
                    } else {
                        this.donations = true;
                        this.average_gift = 0;
                    }

                    if (res.donations.amount_recent !== null && res.donations.amount_recent !== "") {
                        this.most_recent = res.donations.amount_recent;
                    } else {
                        this.most_recent = 0.00;
                    }

                    this.allDonations = res.donations.allDonations;
                    this.allSubscriptions = res.donations.subscriptions;

                    if (this.allDonations.length > 3){
                        this.temp_donations = this.allDonations.slice(0,3);
                    } else {
                        this.temp_donations = this.allDonations;
                    }

                    if (this.allSubscriptions.length > 3){
                        this.temp_subscriptions = this.allSubscriptions.slice(0,3);
                    } else {
                        this.temp_subscriptions = this.allSubscriptions;
                    }
                    // =================== Assigning Data ================================================================= //
                    this.loader.stopLoading();
                }

                this.loader.stopLoading();
            }, (err: any) => {
                let message = err.message;
                if (err.error.http_code === 401) {
                    // const data = {
                    //     auth: 'Bearer ' + this.loginData.auth.access_token,
                    //     authorization: 'Bearer ' + this.loginData.auth.refresh_token
                    // };
                    // this.apiService.updateToken(data).subscribe(res => {
                    //     this.loginData.auth.access_token = res['access_token'];
                    //     this.loginData.auth.refresh_token = res['refresh_token'];
                    //     this.storage.set('user', this.loginData);
                    //     localStorage.setItem('auth', this.loginData.auth.access_token);
                    //     localStorage.setItem('refresh_token', this.loginData.auth.refresh_token);
                    //     this.getContributionData();

                    // }, (err: any) => {
                    //     this.toast.presentToastWithOptions((this.alertMessage ? this.alertMessage.error_occured_try_again : 'Error Occurred, Try Again'), (this.alertMessage ? this.alertMessage.error : 'ERROR'), "warning");
                    // });
                }
                this.loader.stopLoading();
            });
        });
    }

    async logOutUser() {
        this.loader.presentLoading().then(() => {
            this.Auth.logoutUser();
            this.event_provider.userlogout();               // calling event
            this.event_provider.refresh_home();               // calling event
            this.logoService.setStatus(false);
            let login_url = '/login';
            this.router.navigate([login_url]);
            this.loader.stopLoading();
        });
    }

    stopSubscription(id) {
        const body = {
            auth: 'Bearer ' + this.loginData.auth.access_token,
            subscriptionid: id
        };
        // Calling API through service to stop subscription
        this.apiService.stopSubscription(JSON.stringify(body)).subscribe((res: any) => {
            if (res.status) {
                this.ngOnInit();
                this.toast.presentToastWithOptions(res.message, "Success", "success");
            } else {
                this.toast.presentToastWithOptions(res.message, "Warning", "warning");

            }
        })
    }

    // Hides and Shows donations data
    see_more_or_less_donations(){
        if (this.temp_donations.length > 3){
            this.temp_donations = this.allDonations.slice(0,3);
        } else {
            this.temp_donations = this.allDonations;
        }
    }

    // Hides and Shows subscriptions data
    see_more_or_less_subs(){
        if (this.temp_subscriptions.length > 3){
            this.temp_subscriptions = this.allSubscriptions.slice(0,3);
        } else {
            this.temp_subscriptions = this.allSubscriptions;
        }
    }

    gotobackPage() {
        if(this.theme.ch_id === '1000'){
            this.menu.toggle();
        } else {
            this.location.back();
        }
    }

    setAnalytics() {
        let dev_id = '';
        let device = 'web';
        // preparing device data

            dev_id = this.device.uuid;
            if (dev_id){
                device =  this.platform.platforms()[0];
            }

        // preparing duration data
        let end_time_analytics = moment();
        var duration:any = moment.duration(end_time_analytics.diff(this.start_time_analytics)).asSeconds();

        // preparing user data
        let userId = this.loginData ? this.loginData.user.id : 'guest';

        // preparing data for analytics
        const body = JSON.stringify({
            church_id: this.ch_id,
            latitude: this.theme.latitude ? this.theme.latitude : "",
            longitude: this.theme.longitude ? this.theme.longitude : "",
            timespent: duration,
            module: this.page_name,
            custom_name: this.custom_name,
            ip_address: "",
            device: device,
            device_id: dev_id,
            client_type: userId,
            event_type: ""
        });
        // calling function from service to update Analytics Data
        this.analytics.setAnalytics(body).subscribe((res: any) => {

        })
    }

    ionViewDidLeave(){
        this.setAnalytics();
    }
    ionViewDidEnter(){
        // setting start time for analytics
        this.start_time_analytics = moment();
        this.event_provider.hidefooter(true, 'other');
    }
}
